.login {
  /* background-image: url('../../assets/images/login-bg-1.jpeg'); */
  /* background-repeat: round; */
  height: 100vh;
  margin: 0;
  padding: 0;
}

.login-bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
}
.card {
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  border: none;
  padding: 30px 15px;
}
.header-text {
  font-size: 24px;
  font-weight: bold;
}

.top-spacing {
  padding-top: 10%;
}
.error {
  color: red;
}

@media screen and (max-width: 600px) {
  .top-spacing {
    margin-top: 30px;
  }
}

.rel {position: relative;}
.hide_pwd, .show_pwd {position: absolute;top: 42px;right: 10px;}